* {
    box-sizing: border-box
}

:root {
    --sans: 1em/1.6 system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue, Fira Sans, sans-serif;
    --mono: SFMono-Regular, Consolas, 'Liberation Mono', Menlo, Courier, 'Courier New', monospace;
    --c1: #0074d9;
    --c2: #eee;
    --c3: #fff;
    --c4: #000;
    --c5: #fff;
    --c6: #ccc;
    --cs: rgba(0, 0, 0, 0.2);
    --m1: 8px;
    --rc: 8px;
    --ts: 0.3s;
}

@media (prefers-color-scheme: dark) {
    :root {
        --c2: #333;
        --c3: #1e1f20;
        --c4: #fff;
        --cs: rgba(255, 255, 255, 0.1);
    }
}

html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

body {
    margin: 0;
    font: var(--sans);
    font-weight: 400;
    font-style: normal;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    background-color: var(--c3);
    color: var(--c4);
}

img,
iframe {
    border: none;
    max-width: 100%
}

a {
    color: var(--c1);
    text-decoration: none
}

a:hover {
    color: var(--c1);
    text-decoration: underline
}

hr {
    border: 0;
    border-bottom: 1px solid var(--c4);
}


/* Headlines */

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0.6em 0;
    font-weight: normal
}

h1 {
    font-size: 2.625em;
    line-height: 1.2
}

h2 {
    font-size: 1.625em;
    line-height: 1.2
}

h3 {
    font-size: 1.3125em;
    line-height: 1.24
}

h4 {
    font-size: 1.1875em;
    line-height: 1.23
}

h5,
h6 {
    font-size: 1em;
    font-weight: bold
}


/* Table */

table {
    border-collapse: collapse;
    border-spacing: 0;
    margin: 1em 0
}

th,
td {
    text-align: left;
    vertical-align: top;
    border: 1px solid;
    padding: 0.4em
}

thead,
tfoot {
    background: var(--c2)
}


/* Rounded Corners*/

pre,
code,
input,
select,
textarea,
button,
img {
    border-radius: var(--rc)
}


/* Forms */

label {
    margin: 4px;
}

input,
select,
textarea {
    font-size: 1em;
    color: var(--c4);
    background: var(--c2);
    border: 0;
    padding: 0.6em;
    min-width: 7em
}

button,
input[type=submit],
input[type=reset],
input[type="button"] {
    -webkit-appearance: none;
    font-size: 1em;
    display: inline-block;
    color: var(--c5);
    background: var(--c1);
    border: 0;
    padding: 0.6em;
    cursor: pointer;
    text-align: center
}

button:hover,
button:focus,
input:hover,
textarea:hover,
select:hover {
    opacity: 0.8
}

button:disabled {
    background: var(--c6)
}

fieldset {
    border: 0;
    display: inline-block;
}

@media only screen and (max-width: 768px) {
    fieldset {
        display: block;
    }
}